import React, { Component } from 'react';
import '../Square.css';
import '../../Container/Container.css';
import Square from '../Square';

class Squares2x3 extends Component {
  
	render() {
		return (
      	<div className="Container Container2x3">
      		<Square /><Square />
	         <Square /><Square />
	         <Square /><Square />
      	</div>	
		);
	}
}

export default Squares2x3;


