import React, { Component } from 'react';
import '../Square.css';
import '../../Container/Container.css';
import Square from '../Square';


class Squares2x2 extends Component {
	constructor() {
    super();
    this.last_img; //Последняя показанная картинка
	 this.img_root = '../../../../images/'; //Путь к папке с картинками
	 this.click_flag = 1;
	 this.count_click = 0; //Кол-во кликов
	 this.arrayOfImages = [1,1,2,2]; //Массив расположения картинок
	 this.mixedArrayOfImages = this.mix(this.arrayOfImages);
	 //this.dataState;
  	}

  	mix(arrToMix) {
      var index, valueIndex; 
      for (var i=0; i<=arrToMix.length-1; i++) {
        index = Math.floor(Math.random()*i);
        valueIndex = arrToMix[index];
        arrToMix[index] = arrToMix[i];
        arrToMix[i] = valueIndex;
      }
      return arrToMix;
  	}



  handleClick2 = (e) => {
  	const square = e.target;
  	const dataState = e.target.getAttribute('data-state');
    if (dataState === '0' && this.click_flag === 1){
    	console.log(dataState);
      if(this.count_click === 0){ //Если первый клик по закрытому полю
        this.count_click++;
        this.last_img = e.target.getAttribute('id');
        console.log(this.last_img);
        square.setAttribute('data-state', '2');
        square.style.backgroundImage = 'url('+ this.img_root + this.last_img + '.jpg';
        //$(this).data('state',1).attr('data-state',1).css('backgroundImage', 'url(' + this.img_root + this.last_img.substr(3,1) + '.jpg)');
      }
    }
  }
  

  makeSquares(el) {
  		const ids = el.ids;
		const arrayOfSquares = ids.map((id,index) =>
			<Square key={index} id={id}  data-state="0" onClick={this.handleClick2} />
		);
		return arrayOfSquares;
  	}
	render() {
		const ids = this.mixedArrayOfImages;
		console.log(this.mix(this.arrayOfImages));
		//(()=> {alert("vse ok");})();
		return (
      	<div className="Container Container2x2">
      		{this.makeSquares({ids})}

      	</div>	
		);
	}
}

export default Squares2x2;


