import React, { Component } from 'react';
import './Square.css';


class Square extends Component {


  
  state = {'visibility': 'hidden'};
  makeDiv(div){
      return div;
  }
  
  handleClick  = () => {
    if (this.state.visibility === 'hidden') {
      this.setState({'visibility': 'visible'});  
    } else {this.setState({'visibility': 'hidden'});} 
  } 
  
	render() {
    const {visibility} = this.state;
		return (this.makeDiv(<div onClick={this.handleClick} className="Square" {...this.props}>
          <img style={{visibility}} src="http://www.inspiresport.com/wp-content/uploads/2017/09/Logo.jpg" alt="" />
    </div>)
		);
	}
}

export default Square;


